<template>
  <div style="width:100%;height:100%">
      <div style="width:100%;height:100%" id="final">
        <img class="happy" src="../assets/happy.png">
        <img class="heart" src="../assets/heart.png" @click="turn2Next">
        <img v-if="imgUrl" :src="imgUrl" alt="" class="output-real-photo">
        <p class="note">
            {{note}}
        </p>
      </div>
      <img v-if="finalImg" :src="finalImg" alt="" class="final-img">
      
  </div>

</template>

<script>
import html2canvas from 'html2canvas'
export default {
  name: 'outputPhoto',
  data () {
    return {
      imgUrl:this.$store.state.imgUrl,
      note:this.$store.state.note,
      finalImg:''
    }
  },
  methods:{
      turn2Next(){
          console.log(this.$store)
      }
  },
  mounted(){
      var self=this;
    html2canvas(document.querySelector('#final'), {allowTaint: false, taintTest: false}).
    then(function (canvas) {
        var imgUri = canvas.toDataURL("image/png").replace("image/png", "image/octet-stream"); // 获取生成的图片的url  
        self.$set(self,'finalImg',imgUri)
    });
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>


