<template>
  <div class="main">
    <section class="fly-group">
        <figure class="share">
        </figure>
        <section class="output">
            <output-photo></output-photo>
        </section>
                    <h5 class="success-tip">长按图片保存到手机相册</h5>
        <figure class="success"></figure>
    </section>
  </div>

</template>

<script>
import outputPhoto from './outputPhoto'
export default {
  name: 'forth',
  data () {
    return {
      per: 0
    }
  },
  components:{
      outputPhoto
  },
  methods:{
      turn2Next(){
          this.isClicked=true;
        setTimeout(()=>{
            this.$router.push({name:'second'})
        },1000)
      }
  },
  mounted(){
      var inter=setInterval(()=>{
          this.per+=10;
          if(this.per==100){
            clearInterval(inter)
          }
      },120)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
    @import './common.less';
</style>



